/* contacting */
@media (max-width: 790px) {
  .contacting {
    &__wrapper {
      flex-direction: column;
    }

    &__text-side {
      background: none;
    }

    &__title {
      margin-top: 50px;
      margin-bottom: 50px;
      text-align: left;
    }
  }
}

@media (max-width: 456px) {
  .contacting {
    &__title {
      margin-top: 0;
      margin-bottom: 0;
      font-size: 50px;
    }

    &__text-side {
      padding: 0 15px;
    }

    &__form {
      &-side {
        padding: 20px 0;
      }
    }
  }
}

@media (max-width: 456px) {
  .contacting {
    &__form {
      &-action {
        .contacts-input {
          margin-bottom: 30px;
        }

        .form-btn {
          width: 300px;
        }
      }
    }
  }
}

/* advantages */

@media (max-width: 1040px) {
  .advantages {
    &__items {
      justify-content: center;
    }
  }
}
