body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

.vmodal.open .modal__window,
.vmodal.open .modal__overlay {
    opacity: 1;
    z-index: 100;
}

.vmodal.hide .modal__window,
.vmodal.hide .modal__overlay {
    opacity: 1;
    z-index: 100;
}

.vmodal.open .modal__window {
    transform: translateY(100px);
}

.vmodal.open .modal__overlay {
    background: rgba(0, 0, 0, 0.5);
}

.vmodal.hide .modal__window {
    transform: translateY(-200px);
}

.vmodal.hide .modal__overlay {
    background: rgba(0, 0, 0, 0);
}

.modal__window,
.modal__overlay {
    opacity: 0;
    z-index: -1;
}

.container {
    max-width: 1440px;
    margin: 0 auto;
    padding: 20px;
}

.modal {
    &__overlay {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0);
        transition: background 0.2s ese-in;
    }

    &__window {
        width: 600px;
        border-radius: 5px;
        background-color: #fff;
        margin: 0 auto;
        transform: translateY(-200px);
        transition: transform 0.2s ease-in;
    }

    &__header {
        padding: 5px 10px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #eee;
    }

    &__title,
    &__close {
        font-size: 1.5rem;
    }

    &__close {
        cursor: pointer;
    }

    &__body {
        padding: 5px 10px;
    }

    &__footer {
        padding: 5px 10px;
        border-top: 1px solid #eee;
        display: flex;
        justify-content: flex-end;
    }

    &__btn {
        &-ok {
            cursor: pointer;
            background-color: green;
            margin-right: 10px;
            border: none;
            outline: none;
            padding: 5px;
            border-radius: 3px;
            color: #fff;
        }

        &-cancel {
            cursor: pointer;
            background-color: tomato;
            border: none;
            outline: none;
            padding: 5px;
            border-radius: 3px;
            color: #fff;
        }
    }

}