.hero {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background-color: var(--quill-gray);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(var(--color-black), 0.05);
  }

  &__container {
    position: relative;
    height: 100%;
  }

  &__title {
    margin: 0 0 40px;
    max-width: 560px;
    font-weight: 700;
    font-size: 72px;
  }

  &__descr {
    margin: 0;
    font-size: 18px;
  }

  &__image {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 260px;
    height: 100%;
  }
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;

  .swiper-slide {
    height: 100%;
  }

  .hero-slider__controls {
    position: absolute;
    left: 0;
    right: 0;
    top: auto;
    bottom: 60px;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .slider-button-next,
  .slider-button-prev {
    border-radius: 50%;
    width: 56px;
    height: 56px;
    cursor: pointer;
  }

  .slider-button-next {
    background: url("../../img/slider-arrow-next.png") no-repeat center;
  }

  .slider-button-prev {
    background: url("../../img/slider-arrow-prev.png") no-repeat center;
  }

  .slider-button-next::after,
  .slider-button-prev::after {
    font-size: 0;
  }

  .hero-slider-next-btn {
    margin-right: 30px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.35);
    background-color: #d6a557;
  }

  .hero-slider-prev-btn {
    border-radius: 58px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.35);
    background-color: #d6a557;
  }

  .swiper-pagination-bullets {
    width: auto;
  }

  .swiper-pagination-bullet-active {
    border: 1px solid #d6a557 !important;
    background: #d6a557 !important;
  }

  .swiper-pagination-bullet {
    border: 1px solid #fff;
    border-radius: 50%;
    width: 8px;
    height: 8px;
    background: transparent;
    opacity: 1;

    &:not(:last-child) {
      margin-right: 8px;
    }
  }

  .banner-pag {
    position: static;
    margin-left: 40px;
  }
}
