.about {
  &__wrapper {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 100%;
    max-width: 1440px;
  }

  &__top {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 30px 64px 128px;

    &-content {
      flex-basis: 50%;
      padding-left: 100px;
    }
  }

  &__title {
    position: absolute;
    left: -129px;
    top: 25%;
    padding-left: 17px;
    font-weight: 700;
    font-size: 64px;
    line-height: 72px;
    white-space: nowrap;
    text-align: right;
    color: var(--color-secondary-black);
    transform: rotate(-90deg);
  }

  &__text {
    padding-right: 70px;
    font-size: 18px;
    line-height: 24px;
    color: var(--color-secondary-gray4);
  }

  &__bottom {
    padding: 30px 64px 100px;

    &-title {
      font-weight: 700;
      font-size: 32px;
      line-height: 36px;
      color: #212429;
    }

    &-items {
      display: flex;
      justify-content: space-between;
      list-style: none;
    }

    &-item {
      width: 172px;
      height: 240px;

      img {
        width: 100%;
        object-fit: cover;
      }
    }
  }
}
