.header {
  width: 100%;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 36px;
    padding: 32px 64px 0;
  }

  &__logo {
    flex-grow: 0;
    width: 288px;
    height: 64px;
  }

  &__contacts {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
  }

  &__phones {
    &-list {
      display: flex;
      flex-direction: column;
      margin-right: 32px;
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.03em;
      color: var(--color-secondary-black);
    }
  }

  &__buttons {
    display: flex;
  }

  &__btn {
    padding: 18px 32px;
    height: 60px;
    font-size: 16px;
    line-height: 20px;
  }

  &-call__btn {
    margin-right: 32px;
  }

}

.phone-icon,
.price-icon {
  margin-right: 10px;
  width: 24px;
  height: 24px;
}

.phone-icon {
  fill: var(--color-primary-default);
}

.price-icon {
  fill: var(--color-white);
}

@media (max-width: 1140px) {
  .header {
    &__container {
      flex-direction: column;
    }

    &__logo {
      margin-bottom: 20px;
    }
  }
}

@media (max-width: 1140px) {
  .header {
    &__contacts {
      flex-direction: column;
      text-align: center;
    }

    &__phones {
      margin-bottom: 20px;
    }
  }
}

@media (max-width: 674px) {
  .header {
    &__buttons {
      flex-direction: column;
      justify-content: center;
    }

    &-call__btn {
      margin: 0 0 20px;
    }
  }
}
