.slider-catalog {
  margin-bottom: 100px;
}

.catalog {
  &__title {
    font-weight: 700;
    font-size: 64px;
    line-height: 72px;
    color: var(--color-secondary-black);
  }

  &__text {
    margin-bottom: 70px;
    font-size: 18px;
    line-height: 24px;
    color: var(--color-secondary-gray4);
  }

  &__slider {
    position: relative;

    .swiper-pagination {
      position: static;
    }

    .swiper-pagination-bullet:not(:last-child) {
      margin-right: 10px;
    }

    .swiper-pagination-bullet-active {
      background: #d6a557;
    }
  }

  &-slider {
    padding-bottom: 82px;

    &__controls {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto;
    }

    &__next-btn,
    &__prev-btn {
      margin-right: 50px;
      border: 1.5px solid #d6a557;
      border-radius: 50%;
      width: 56px;
      height: 56px;
      box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.35);
      background-color: #d6a557 !important;
      cursor: pointer;
    }

    &__next-btn {
      background: url("../../img/slider-arrow-next.png") no-repeat center;
    }

    &__prev-btn {
      background: url("../../img/slider-arrow-prev.png") no-repeat center;
    }
  }

  &-slide {
    &__item {
      position: relative;
      min-height: 170px;

      .label {
        position: absolute;
        left: 16px;
        top: 13px;
        z-index: 1;
      }
    }

    &__image {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 0;
      margin-bottom: 16px;
      width: 100%;
    }

    &__directory {
      margin-bottom: 12px;
      font-size: 16px;
      line-height: 20px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: var(--color-primary-default);
    }
  }
}

.catalog-btn {
  position: absolute;
  right: 0;
  bottom: 96px;
  z-index: 15;
}

.btn {
  &__link {
    display: inline-block;
    border: 1.5px solid var(--color-primary-default);
    border-radius: 4px;
    padding: 18px 32px;
    font-size: 16px;
    line-height: 20px;
    color: var(--color-primary-default);
    transition: 0.5s ease;

    &:hover {
      color: var(--color-white);
      background-color: var(--color-primary-default);
    }
  }
}
