html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  @include stop-scroll;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.visually-hidden {
  @include visually-hidden;
}

.container {
  margin: 0 auto;
 /* padding: 0 40px;*/
  max-width: 1440px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

/* BUTTONS*/

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.btn__primary {
  display: flex;
  align-items: center;
  border-radius: 4px;
  padding: 20px 27px;
  box-shadow: 3px 3px 4px rgba(84, 60, 18, 0.75);
  font-size: 18px;
  color: var(--color-white);
  background: linear-gradient(0deg, #c3063c 0%, #c3063c 0.01%, #f81e5c 100%);
  //transition: all 0.3s ease-in;
  
  &:hover {
    background: var(--color-primary-default);
  }
}

.btn__secondary {
  display: flex;
  align-items: center;
  border-radius: 4px;
  color: var(--color-primary-default);
  background: rgba(248, 30, 92, 0.15);
  
  &:hover {
    color: var(--color-white);
    background: var(--buttons-gradient);
    
    .phone-icon {
      fill: var(--color-white);
    }
  }
}
