.footer {
  position: relative;
  padding-top: 40px;
  width: 100%;
  background: var(--color-secondary-black);

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 48px;
    border-top: 1px solid var(--color-secondary-gray3);
    width: 100%;
    height: 1px;
  }

  &__wrapper {
    position: relative;
  }

  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__menu {
    &-title {
      margin-bottom: 36px;
      font-weight: 700;
      font-size: 24px;
      line-height: 24px;
      color: var(--color-secondary-gray2);
    }

    &-item {
      &:not(:last-child) {
        margin-bottom: 12px;
      }
    }

    &-link {
      font-size: 16px;
      line-height: 24px;
      color: var(--color-secondary-gray3);
    }
  }

  &__address {
    margin-bottom: 22px;
    max-width: 284px;
    font-size: 16px;
    line-height: 24px;
    color: var(--color-secondary-gray3);
  }

  &__phones {
    margin-bottom: 32px;
    max-width: 150px;

    a {
      font-weight: 700;
      font-size: 16px;
      line-height: 24px;
      color: var(--color-secondary-gray3);
    }
  }

  &__tabs {
    &-items {
      display: flex;
      margin-bottom: 24px;
      border-bottom: 1px solid var(--color-secondary-gray2);
    }

    &-item {
      padding-bottom: 10px;
      font-size: 16px;
      line-height: 24px;
      color: var(--color-secondary-gray2);
      cursor: pointer;

      &:not(:last-child) {
        margin-right: 23px;
      }
    }

    &-content {
      margin-bottom: 58px;

      p {
        margin: 0;
        padding: 0;
        font-size: 16px;
        line-height: 24px;
        color: var(--color-secondary-gray3);
      }

      &-item {
        width: 350px;
        height: 96px;
      }
    }
  }

  &__bottom {
    position: absolute;
    left: 0;
    bottom: -8px;
    display: flex;
    font-size: 14px;
    line-height: 20px;
    color: var(--color-secondary-gray3);
  }

  &__copyright {
    margin-right: 75px;
  }
}

[data-tab-content] {
  display: none;
}

.active[data-tab-content] {
  display: block;
}

.active[data-tab-target] {
  border-bottom: 3px solid var(--color-secondary-gray3);
}
