.section__advantages {
  margin-bottom: 80px;
}

.advantages {
  &__container {
    max-width: 1070px;
  }

  &__title {
    font-weight: 700;
    font-size: 64px;
    line-height: 75px;
    text-align: center;
    color: var(--color-secondary-black);
  }

  &__items {
    display: grid;
    grid-auto-flow: row;
    grid-gap: 85px;
    grid-template: repeat(auto-fit, 147px) / repeat(auto-fit, 286px);
  }

  &__item {
    position: relative;
    display: grid;
    align-items: center;
    grid-template: 1fr 1fr / 1fr 1fr;
    height: 147px;
  }

  &__number {
    grid-column: 1 / 2;
    grid-row: 1 / 3;
    font-weight: 900;
    font-size: 200px;
    line-height: 234px;
    color: var(--color-primary-default);
  }

  &__icon {
    display: flex;
    align-items: center;
    align-self: start;
    justify-content: center;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    border-radius: 48px;
    width: 60px;
    height: 60px;
    box-shadow: 0 20px 20px -4px rgba(195, 6, 60, 0.15);
  }

  &__text {
    align-self: end;
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    width: 170px;
    font-size: 16px;
    line-height: 20px;
    color: var(--color-secondary-gray4);
  }
}
