.main-catalog {
  &__nav {
    position: absolute;
    left: 0;
    top: 25px;
    z-index: 10;
    display: none;
    margin: 50px auto;

    &.active {
      display: block;
    }
  }

  &__items,
  &__subitems {
    list-style: none;
    margin: 0;
    border-radius: 4px;
    padding: 16px 0;
    width: 340px;
    box-shadow: 0 15px 25px -5px rgba(0, 0, 0, 0.15);
    background: #fff;
  }

  &__item,
  &__subitem {
    padding: 14px 32px;
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    line-height: 24px;
    color: var(--color-secondary-gray4);
    transition: 0.5s;
  }

  &__item:hover,
  &__subitem:hover {
    padding: 14px 0 14px 42px;
    color: #212429;
    background-color: #fbf4ea;
    cursor: pointer;
  }

  &__item {
    position: relative;
  }

  &__link {
    text-decoration: none;
    color: inherit;
  }

  &__subitems {
    position: absolute;
    left: 100%;
    top: -9999em;
    opacity: 0;
    transition: opacity 0.3s ease-in;
  }

  &__item-sub:hover &__arrow {
    transform: scaleX(-1);
  }

  &__item-sub:hover &__subitems {
    top: -16px;
    opacity: 1;
  }

  &__arrow {
    position: absolute;
    right: 0.7em;
    top: 1.3em;
    width: 10px;
    height: 10px;
    transition: 0.5s;
  }
}
