.about {
  &__slider {
    max-width: 644px;
  }

  .swiper-container {
    overflow: hidden;
    width: 644px;
    height: 100%;
  }

  .about-slider__controls {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &-slider__button-next,
  &-slider__button-prev {
    margin-right: 50px;
    border: 1.5px solid #d6a557;
    border-radius: 50%;
    width: 56px;
    height: 56px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.35);
    background-color: #d6a557 !important;
    cursor: pointer;
  }

  .about-slider__button-next {
    background: url("../../img/slider-arrow-next.png") no-repeat center;
  }

  .about-slider__button-prev {
    background: url("../../img/slider-arrow-prev.png") no-repeat center;
  }

  .swiper-button-next::after,
  .swiper-button-prev::after {
    font-size: 0;
  }

  .swiper-pagination-bullet:not(:last-child) {
    margin-right: 10px;
  }

  .swiper-pagination-bullet-active {
    background: #d6a557;
  }
}
