.contacting {
  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__text-side {
    flex-basis: 50%;
    padding-left: 64px;
    background: url("../.././img/contacting-bg.jpg") no-repeat bottom;
    background-size: contain;
  }

  &__title {
    margin-top: 95px;
    margin-bottom: 71px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-style: normal;
    font-size: 64px;
    line-height: 75px;
    color: #212429;
  }

  &__texts {
    max-width: 510px;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: #495057;
  }

  &__form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 32px;
    background: #fff;

    &-side {
      display: flex;
      flex-basis: 50%;
      justify-content: center;
      padding: 100px 0;
      background: #c3063c;
    }

    &-wrapper {
      border-radius: 4px;
      max-width: 442px;
    }

    &-input {
      margin-top: 34px;
      margin-bottom: 15px;
      width: 100%;
      height: 68px;

      span {
        display: block;
        margin-bottom: 10px;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #495057;

        sup {
          font-size: 16px;
          line-height: 20px;
          color: #c3063c;
        }
      }

      input {
        outline: none;
        border: none;
        border-radius: 4px;
        padding: 22px 24px;
        width: 100%;
        background: #dde2e5;
      }
    }

    &-action {
      margin-top: 32px;
      font-family: "Roboto", sans-serif;
      color: #495057;

      .my-action {
        margin-bottom: 16px;
        font-size: 16px;
        line-height: 20px;
        color: #495057;
      }

      .contacts-input,
      .price-input {
        position: relative;
        display: block;
        margin-bottom: 14px;
        font-size: 16px;
        line-height: 20px;

        span {
          position: absolute;
          left: 30px;
          top: 0;

          &::before {
            content: "";
            position: absolute;
            left: -30px;
            top: 0;
            border: 2px solid #495057;
            border-radius: 50%;
            width: 20px;
            height: 20px;
          }

          &::after {
            content: "";
            position: absolute;
            left: -25px;
            top: 5px;
            border-radius: 50%;
            width: 10px;
            height: 10px;
            background: #c3063c;
            opacity: 0;
          }
        }

        input {
          &:checked + span::after {
            opacity: 1;
          }
        }
      }

      .personal-data {
        position: relative;
        display: block;
        margin-bottom: 40px;

        span {
          position: absolute;
          left: 30px;
          top: 0;
          transition: opacity 0.5s;

          &::before {
            content: "";
            position: absolute;
            left: -30px;
            top: 0;
            border: 2px solid #495057;
            width: 20px;
            height: 20px;
          }

          &::after {
            content: "";
            position: absolute;
            left: -30px;
            top: 0;
            width: 20px;
            height: 20px;
            background: url("../.././img/check.png");
            opacity: 0;
          }
        }

        input {
          &:checked + span::after {
            opacity: 1;
          }
        }

        a {
          display: inline-block;
          margin-left: 30px;
          text-decoration: underline;
          color: #c3063c;
        }
      }

      .required-fields {
        margin-bottom: 21px;

        span {
          color: #c3063c;
        }
      }

      .form-btn {
        margin-bottom: 32px;
        outline: none;
        border: none;
        border-radius: 4px;
        width: 351px;
        height: 64px;
        color: #fff;
        background: #c3063c;

        &:hover {
          background-color: #ff3243;
          cursor: pointer;
        }
      }
    }
  }
}
