.header {
  .bottom__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0 64px 35px;
  }

  &__nav {
    display: flex;
    flex-grow: 1;
    align-items: center;
    justify-content: space-between;
    position: relative;

    &-list {
      display: flex;
      flex-grow: 1;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
    }

    &-item {
      display: inline-block;
      margin-right: 1px;
    }

    &-link {
      display: inline-block;
      border-radius: 4px;
      padding: 20px 36px;
      width: 208px;
      box-shadow: 3px 3px 4px rgba(84, 60, 18, 0.75);
      font-size: 18px;
      line-height: 24px;
      text-align: center;
      color: var(--color-white);
      background: linear-gradient(180deg, #e4b563 0%, #c08f46 100%);
    }
  }

  &-catalog__btn {
    margin-right: 2vw;
  }

  &-menu__icon {
    margin-right: 10px;
    width: 20px;
    height: 20px;
  }

  &__input {
    border: none;
    width: 53px;
    font-size: 18px;
    line-height: 24px;
    color: var(--color-secondary-gray4);
    background: transparent;
  }

  &-icon {
    width: 16px;
    height: 16px;
    fill: var(--color-primary-default);
  }
}

@media (max-width: 1430px) {
  .header {
    &__nav {
      justify-content: center;

      &-link {
        display: inline-block;
      }

      &-item {
        margin-bottom: 20px;

        &:last-child a {
          margin-right: 0;
        }
      }
    }

    &-catalog__btn {
      order: -1;
      margin-bottom: 25px;
    }
  }
}

@media (max-width: 425px) {
  .header {
    &-bottom {
      flex-direction: column;
    }

    &__container {
      margin-bottom: 20px;
    }

    &-catalog__btn {
      margin-right: 0;
      width: 257px;
      text-align: center;
    }

    &__nav {
      &-list {
        width: 257px;
      }

      &-item {
        width: 100%;
      }

      &-link {
        width: 100%;
      }
    }
  }
}
