.section-title {
  font-weight: 700;
  font-size: 64px;
  line-height: 72px;
  color: var(--color-secondary-black);
}

.directions {
  &__inner {
    position: relative;
    z-index: 5;
    margin: 0 auto;
    padding-bottom: 108px;
    max-width: 1440px;
    //background: #f8fafb;
  }

  &__items {
    display: grid;
    grid-template: 1fr 1fr / 2fr 1fr 1fr;
    grid-template-areas: "first-block second-block third-block" "first-block fourth-block fourth-block";
    list-style: none;
    margin: 0;
    padding: 0;
    gap: 24px;
  }

  &__item {
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__item1 {
    grid-area: first-block;
    //height: 644px;
  }

  &__item2 {
    grid-area: second-block;
    background-size: cover;
  }

  &__item3 {
    grid-area: third-block;
    background-size: cover;
  }

  &__item4 {
    grid-area: fourth-block;
    background-size: cover;
  }

  .directions__item-title {
    position: absolute;
    left: 0;
    right: 0;
    font-weight: 700;
    text-align: center;
    color: var(--color-secondary-black);
  }

  &__item-title1 {
    font-size: 56px;
    line-height: 64px;
  }

  &__item-title2 {
    font-size: 24px;
    line-height: 28px;
  }

  &__item-title3 {
    font-size: 40px;
    line-height: 47px;
  }
}
