:root {
  --color-black: #000;
  --color-white: #fff;
  --color-primary-default: #c3063c;
  /*$primary*/
  --color-primary-hover: #f81e5c;
  --color-primary-pressed: #820428;
  --color-secondary-black: #212429;
  --color-secondary-gray1: #f8f9fa;
  --color-secondary-gray2: #dde2e5;
  --color-secondary-gray3: #acb5bd;
  --color-secondary-gray4: #495057;
  --color-linen: #fbf4ea;
  --buttons-gradient: linear-gradient(0deg, var(--color-primary-default) 0%, var(--color-primary-default) 0.01%, var(--color-primary-hover) 100%);
  --menu-buttons-gradient: linear-gradient(180deg, #ffe8c0 0%, #dcb474 48.96%, #b87d25 100%);
  --search-button-gradient: linear-gradient(180deg, #cecece 0%, #f1f1f1 100%);
}

@font-face {
  font-family: "Roboto-Bold";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../Roboto-Bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Regular";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Light";
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../Roboto-Light.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Medium";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../Roboto-Medium.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

