html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  /* padding: 0 40px;*/
  max-width: 1440px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

/* BUTTONS*/
.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.btn__primary {
  display: flex;
  align-items: center;
  border-radius: 4px;
  padding: 20px 27px;
  box-shadow: 3px 3px 4px rgba(84, 60, 18, 0.75);
  font-size: 18px;
  color: var(--color-white);
  background: linear-gradient(0deg, #c3063c 0%, #c3063c 0.01%, #f81e5c 100%);
}

.btn__primary:hover {
  background: var(--color-primary-default);
}

.btn__secondary {
  display: flex;
  align-items: center;
  border-radius: 4px;
  color: var(--color-primary-default);
  background: rgba(248, 30, 92, 0.15);
}

.btn__secondary:hover {
  color: var(--color-white);
  background: var(--buttons-gradient);
}

.btn__secondary:hover .phone-icon {
  fill: var(--color-white);
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

.vmodal.open .modal__window,
.vmodal.open .modal__overlay {
  opacity: 1;
  z-index: 100;
}

.vmodal.hide .modal__window,
.vmodal.hide .modal__overlay {
  opacity: 1;
  z-index: 100;
}

.vmodal.open .modal__window {
  transform: translateY(100px);
}

.vmodal.open .modal__overlay {
  background: rgba(0, 0, 0, 0.5);
}

.vmodal.hide .modal__window {
  transform: translateY(-200px);
}

.vmodal.hide .modal__overlay {
  background: rgba(0, 0, 0, 0);
}

.modal__window,
.modal__overlay {
  opacity: 0;
  z-index: -1;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 20px;
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0);
  transition: background 0.2s ese-in;
}

.modal__window {
  width: 600px;
  border-radius: 5px;
  background-color: #fff;
  margin: 0 auto;
  transform: translateY(-200px);
  transition: transform 0.2s ease-in;
}

.modal__header {
  padding: 5px 10px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
}

.modal__title, .modal__close {
  font-size: 1.5rem;
}

.modal__close {
  cursor: pointer;
}

.modal__body {
  padding: 5px 10px;
}

.modal__footer {
  padding: 5px 10px;
  border-top: 1px solid #eee;
  display: flex;
  justify-content: flex-end;
}

.modal__btn-ok {
  cursor: pointer;
  background-color: green;
  margin-right: 10px;
  border: none;
  outline: none;
  padding: 5px;
  border-radius: 3px;
  color: #fff;
}

.modal__btn-cancel {
  cursor: pointer;
  background-color: tomato;
  border: none;
  outline: none;
  padding: 5px;
  border-radius: 3px;
  color: #fff;
}

.header {
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 36px;
  padding: 32px 64px 0;
}

.header__logo {
  flex-grow: 0;
  width: 288px;
  height: 64px;
}

.header__contacts {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end;
}

.header__phones-list {
  display: flex;
  flex-direction: column;
  margin-right: 32px;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.03em;
  color: var(--color-secondary-black);
}

.header__buttons {
  display: flex;
}

.header__btn {
  padding: 18px 32px;
  height: 60px;
  font-size: 16px;
  line-height: 20px;
}

.header-call__btn {
  margin-right: 32px;
}

.phone-icon,
.price-icon {
  margin-right: 10px;
  width: 24px;
  height: 24px;
}

.phone-icon {
  fill: var(--color-primary-default);
}

.price-icon {
  fill: var(--color-white);
}

@media (max-width: 1140px) {
  .header__container {
    flex-direction: column;
  }
  .header__logo {
    margin-bottom: 20px;
  }
}

@media (max-width: 1140px) {
  .header__contacts {
    flex-direction: column;
    text-align: center;
  }
  .header__phones {
    margin-bottom: 20px;
  }
}

@media (max-width: 674px) {
  .header__buttons {
    flex-direction: column;
    justify-content: center;
  }
  .header-call__btn {
    margin: 0 0 20px;
  }
}

.main-catalog__nav {
  position: absolute;
  left: 0;
  top: 25px;
  z-index: 10;
  display: none;
  margin: 50px auto;
}

.main-catalog__nav.active {
  display: block;
}

.main-catalog__items, .main-catalog__subitems {
  list-style: none;
  margin: 0;
  border-radius: 4px;
  padding: 16px 0;
  width: 340px;
  box-shadow: 0 15px 25px -5px rgba(0, 0, 0, 0.15);
  background: #fff;
}

.main-catalog__item, .main-catalog__subitem {
  padding: 14px 32px;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  line-height: 24px;
  color: var(--color-secondary-gray4);
  transition: 0.5s;
}

.main-catalog__item:hover, .main-catalog__subitem:hover {
  padding: 14px 0 14px 42px;
  color: #212429;
  background-color: #fbf4ea;
  cursor: pointer;
}

.main-catalog__item {
  position: relative;
}

.main-catalog__link {
  text-decoration: none;
  color: inherit;
}

.main-catalog__subitems {
  position: absolute;
  left: 100%;
  top: -9999em;
  opacity: 0;
  transition: opacity 0.3s ease-in;
}

.main-catalog__item-sub:hover .main-catalog__arrow {
  transform: scaleX(-1);
}

.main-catalog__item-sub:hover .main-catalog__subitems {
  top: -16px;
  opacity: 1;
}

.main-catalog__arrow {
  position: absolute;
  right: 0.7em;
  top: 1.3em;
  width: 10px;
  height: 10px;
  transition: 0.5s;
}

.header .bottom__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 0 64px 35px;
}

.header__nav {
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.header__nav-list {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.header__nav-item {
  display: inline-block;
  margin-right: 1px;
}

.header__nav-link {
  display: inline-block;
  border-radius: 4px;
  padding: 20px 36px;
  width: 208px;
  box-shadow: 3px 3px 4px rgba(84, 60, 18, 0.75);
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: var(--color-white);
  background: linear-gradient(180deg, #e4b563 0%, #c08f46 100%);
}

.header-catalog__btn {
  margin-right: 2vw;
}

.header-menu__icon {
  margin-right: 10px;
  width: 20px;
  height: 20px;
}

.header__input {
  border: none;
  width: 53px;
  font-size: 18px;
  line-height: 24px;
  color: var(--color-secondary-gray4);
  background: transparent;
}

.header-icon {
  width: 16px;
  height: 16px;
  fill: var(--color-primary-default);
}

@media (max-width: 1430px) {
  .header__nav {
    justify-content: center;
  }
  .header__nav-link {
    display: inline-block;
  }
  .header__nav-item {
    margin-bottom: 20px;
  }
  .header__nav-item:last-child a {
    margin-right: 0;
  }
  .header-catalog__btn {
    order: -1;
    margin-bottom: 25px;
  }
}

@media (max-width: 425px) {
  .header-bottom {
    flex-direction: column;
  }
  .header__container {
    margin-bottom: 20px;
  }
  .header-catalog__btn {
    margin-right: 0;
    width: 257px;
    text-align: center;
  }
  .header__nav-list {
    width: 257px;
  }
  .header__nav-item {
    width: 100%;
  }
  .header__nav-link {
    width: 100%;
  }
}

.hero {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background-color: var(--quill-gray);
}

.hero::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(var(--color-black), 0.05);
}

.hero__container {
  position: relative;
  height: 100%;
}

.hero__title {
  margin: 0 0 40px;
  max-width: 560px;
  font-weight: 700;
  font-size: 72px;
}

.hero__descr {
  margin: 0;
  font-size: 18px;
}

.hero__image {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0 260px;
  height: 100%;
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.banner-slider .swiper-slide {
  height: 100%;
}

.banner-slider .hero-slider__controls {
  position: absolute;
  left: 0;
  right: 0;
  top: auto;
  bottom: 60px;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner-slider .slider-button-next,
.banner-slider .slider-button-prev {
  border-radius: 50%;
  width: 56px;
  height: 56px;
  cursor: pointer;
}

.banner-slider .slider-button-next {
  background: url("../../img/slider-arrow-next.png") no-repeat center;
}

.banner-slider .slider-button-prev {
  background: url("../../img/slider-arrow-prev.png") no-repeat center;
}

.banner-slider .slider-button-next::after,
.banner-slider .slider-button-prev::after {
  font-size: 0;
}

.banner-slider .hero-slider-next-btn {
  margin-right: 30px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.35);
  background-color: #d6a557;
}

.banner-slider .hero-slider-prev-btn {
  border-radius: 58px;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.35);
  background-color: #d6a557;
}

.banner-slider .swiper-pagination-bullets {
  width: auto;
}

.banner-slider .swiper-pagination-bullet-active {
  border: 1px solid #d6a557 !important;
  background: #d6a557 !important;
}

.banner-slider .swiper-pagination-bullet {
  border: 1px solid #fff;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background: transparent;
  opacity: 1;
}

.banner-slider .swiper-pagination-bullet:not(:last-child) {
  margin-right: 8px;
}

.banner-slider .banner-pag {
  position: static;
  margin-left: 40px;
}

.section-title {
  font-weight: 700;
  font-size: 64px;
  line-height: 72px;
  color: var(--color-secondary-black);
}

.directions__inner {
  position: relative;
  z-index: 5;
  margin: 0 auto;
  padding-bottom: 108px;
  max-width: 1440px;
}

.directions__items {
  display: grid;
  grid-template: 1fr 1fr / 2fr 1fr 1fr;
  grid-template-areas: "first-block second-block third-block" "first-block fourth-block fourth-block";
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 24px;
}

.directions__item {
  position: relative;
}

.directions__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.directions__item1 {
  grid-area: first-block;
}

.directions__item2 {
  grid-area: second-block;
  background-size: cover;
}

.directions__item3 {
  grid-area: third-block;
  background-size: cover;
}

.directions__item4 {
  grid-area: fourth-block;
  background-size: cover;
}

.directions .directions__item-title {
  position: absolute;
  left: 0;
  right: 0;
  font-weight: 700;
  text-align: center;
  color: var(--color-secondary-black);
}

.directions__item-title1 {
  font-size: 56px;
  line-height: 64px;
}

.directions__item-title2 {
  font-size: 24px;
  line-height: 28px;
}

.directions__item-title3 {
  font-size: 40px;
  line-height: 47px;
}

.about__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  max-width: 1440px;
}

.about__top {
  position: relative;
  display: flex;
  justify-content: space-between;
