}

.contacting__form-action .contacts-input,
.contacting__form-action .price-input {
  position: relative;
  display: block;
  margin-bottom: 14px;
  font-size: 16px;
  line-height: 20px;
}

.contacting__form-action .contacts-input span,
.contacting__form-action .price-input span {
  position: absolute;
  left: 30px;
  top: 0;
}

.contacting__form-action .contacts-input span::before,
.contacting__form-action .price-input span::before {
  content: "";
  position: absolute;
  left: -30px;
  top: 0;
  border: 2px solid #495057;
  border-radius: 50%;
  width: 20px;
  height: 20px;
}

.contacting__form-action .contacts-input span::after,
.contacting__form-action .price-input span::after {
  content: "";
  position: absolute;
  left: -25px;
  top: 5px;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  background: #c3063c;
  opacity: 0;
}

.contacting__form-action .contacts-input input:checked + span::after,
.contacting__form-action .price-input input:checked + span::after {
  opacity: 1;
}

.contacting__form-action .personal-data {
  position: relative;
  display: block;
  margin-bottom: 40px;
