}

.contacting__form-action .personal-data span::before {
  content: "";
  position: absolute;
  left: -30px;
  top: 0;
  border: 2px solid #495057;
  width: 20px;
  height: 20px;
}

.contacting__form-action .personal-data span::after {
  content: "";
  position: absolute;
  left: -30px;
  top: 0;
  width: 20px;
  height: 20px;
  background: url("../.././img/check.png");
  opacity: 0;
}

.contacting__form-action .personal-data input:checked + span::after {
  opacity: 1;
}

.contacting__form-action .personal-data a {
