.contacts {
  padding-bottom: 104px;
  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__content {
    flex-basis: 40%;
  }

  &__title {
    margin-bottom: 46px;
    font-weight: 700;
    font-size: 64px;
    line-height: 75px;
    color: var(--color-secondary-black);
  }

  &__adress {
    margin-bottom: 64px;
    max-width: 329px;
    font-size: 18px;
    line-height: 24px;
    color: var(--color-secondary-gray4);
  }

  &__office {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid var(--color-secondary-gray2);
    padding-top: 24px;
    padding-bottom: 48px;

    &-number {
      font-weight: 700;
      font-size: 20px;
      line-height: 28px;
      color: var(--color-secondary-black);
    }

    &-persons {
      font-size: 18px;
      line-height: 24px;
      color: var(--color-secondary-gray4);
    }
  }

  &__phones {
    font-size: 18px;
    line-height: 24px;

    span {
      color: var(--color-secondary-gray4);
    }

    &-number {
      margin-bottom: 24px;
      font-weight: 700;
      color: var(--color-primary-default);
    }
  }

  &__map {
    padding-top: 78px;
  }
}
