.news {
  &__container {
    position: relative;
    margin-bottom: 100px;
  }

  &-section__title {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    font-weight: 700;
    font-size: 64px;
    line-height: 75px;
    color: var(--color-secondary-black);
  }

  .all-news__btn {
    position: absolute;
    left: 43%;
    top: 14px;
  }

  &__items {
    padding-top: 162px;
  }

  &__item {
    border-bottom: 1px solid #dde2e5;
    padding-bottom: 24px;

    &:not(:last-child) {
      margin-bottom: 24px;
    }

    &-wrapper {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    &-title {
      max-width: 289px;
      font-weight: 700;
      font-size: 32px;
      line-height: 36px;
      color: var(--color-secondary-black);
    }

    &-descr {
      max-width: 372px;
      font-size: 18px;
      line-height: 24px;
      color: var(--color-secondary-gray4);
    }

    &-date {
      font-size: 18px;
      line-height: 24px;
      color: var(--color-secondary-gray3);
    }
  }
}
